<template>
<div class="container">  
<!-- Formulario para añadir personas -->

        <section class="form">
            <form action="" class="text-center">
                <input v-model="autor" @keyup.enter="crearPersona" type="text" class="form-control" placeholder="Autor/a">
                <input v-model="nombre" @keyup.enter="crearPersona" type="text" class="form-control" placeholder="Nom cançó">
                <input v-model="edad" @keyup.enter="crearPersona" type="number" name="edad" placeholder="Puntuació" class="form-control">
                
                <!-- Botón para añadir -->
                <input @click="crearPersona" type="button" value="Afegir" class="btn btn-success">
            </form>
        </section>
        
<!-- Tabla donde se muestran los datos -->
        <section class="data">
            <table class="table">
                <thead>
                    <tr>
                        <th scope="col">Autor/a</th>
                        <th scope="col">Títol cançó</th>
                        <th scope="col">Puntuació</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(persona, index) in personas" :key="persona.id">
                        <!--<td>{{ persona.id }}</td>-->
                        <td>
                            <span v-if="formActualizar && idActualizar == index">
                                
                                <!-- Formulario para actualizar -->
                                <input v-model="autorActualizar" type="text" class="form-control">
                            </span>
                            <span v-else>

                                <!-- Dato nombre -->
                                {{ persona.autor }}
                            </span>
                        </td>

                        <td>
                            <span v-if="formActualizar && idActualizar == index">
                                
                                <!-- Formulario para actualizar -->
                                <input v-model="nombreActualizar" type="text" class="form-control">
                            </span>
                            <span v-else>

                                <!-- Dato nombre -->
                                {{ persona.nombre }}
                            </span>
                        </td>
                        <td>
                            <span v-if="formActualizar && idActualizar == index">
                                
                                <!-- Formulario para actualizar -->
                                <input v-model="edadActualizar" type="text" class="form-control">
                            </span>
                            <span v-else>
                                <!-- Dato edad -->
                                {{ persona.edad }}
                            </span>
                        </td>
                        <td>
                            
                            <!-- Botón para guardar la información actualizada -->
                            <span v-if="formActualizar && idActualizar == index">
                                <button @click="guardarActualizacion(index)" class="btn">Guardar</button>
                            </span>
                            <span v-else>
                                
                                <!-- Botón para mostrar el formulario de actualizar -->
                                <button @click="verFormActualizar(index)" class="btn">Actualitzar</button>

                                <!-- Botón para borrar -->
                                <button @click="borrarPersona(index)" class="btn">Borrar</button>
                            </span>
                        </td>
                    </tr>
                </tbody>
            </table>
        </section>
    </div>
</template>

<script>
  export default {
        data() {
        return {
            // Input nombre
            autor: '',
            // Input nombre
            nombre: '',
            // Input edad
            edad: '',
            // Ver o no ver el formulario de actualizar
            formActualizar: false,
            // La posición de tu lista donde te gustaría actualizar 
            idActualizar: -1,
            // Input nombre dentro del formulario de actualizar
            autorActualizar: '',
            // Input edad dentro del formulario de actualizar
            nombreActualizar: '',
            // Input edad dentro del formulario de actualizar
            edadActualizar: '',
            // Lista de personas
            personas: [] 
         }
    },
        methods: {
            crearPersona: function () {
                
                // Anyadimos a nuestra lista
                this.personas.push({
                    id: + new Date(),
                    autor: this.autor,
                    nombre: this.nombre,
                    edad: this.edad
                });
                
                // Vaciamos el formulario de añadir
                this.autor = '';
                this.nombre = '';
                this.edad = '';
            },
            verFormActualizar: function (persona_id) {
                
                // Antes de mostrar el formulario de actualizar, rellenamos sus campos
                this.idActualizar = persona_id;
                this.autorActualizar = this.personas[persona_id].autor;
                this.nombreActualizar = this.personas[persona_id].nombre;
                this.edadActualizar = this.personas[persona_id].edad;
                
                // Mostramos el formulario
                this.formActualizar = true;
            },
            borrarPersona: function (persona_id) {
                
                // Borramos de la lista
                this.personas.splice(persona_id, 1);
            },
            guardarActualizacion: function (persona_id) {
                // Ocultamos nuestro formulario de actualizar
                this.formActualizar = false;
                // Actualizamos los datos
                this.personas[persona_id].autor = this.autorActualizar;
                this.personas[persona_id].nombre = this.nombreActualizar;
                this.personas[persona_id].edad = this.edadActualizar;
            }
        }
    }
</script>

<style scoped>
.form {
  margin-left: 30%;
  margin-right: 30%;
  margin-bottom: 2rem;
}

.form-control {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.btn {
  color:white;
  background-color: #1FBF57;
  margin-left: 0rem;
  margin-right: 1rem;
  margin-top: 0rem;
  margin-bottom: 0rem;
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
}

.form{
  margin-top: 2rem;
}

.data {
  margin-top: 4rem;
}

</style>